<!--// John-->
// end完成
<!--// import RankDetails from '@/view/rank-details.vue'; //榜单详情-->
<template>
  <div class="RankDetails">
    <div class="relative">
      <img class="RankDetails-bg" :src="data.cover_img_url" alt="">
      <div class="RankDetails-bgShadow"></div>
    </div>
    <div class="RankDetails-content">
      <h1 class="RankDetails-h1">{{data.title}}</h1>
      <p class="RankDetails-p">{{data.content}}</p>
      <AddRankCard style="margin-top:40px;"/>
      <div class="text-center">
        <div class="YellowBtn" style="margin-top:40px;" @click="favorites(data.fav_state,data.id)">
          <span v-if="!data.fav_state">收藏</span>
          <span v-else>取消收藏</span>
        </div>
        <div class="RankDetails-collectionCount">{{data.favs}} 人已收藏</div>
        <div style="margin-top:60px;">
          <ProductBar class="text-left" style="margin-top:28px;"  v-for="(productData,i) in productDatas" :key="productData.id" :vData="productData"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import appMixins from '@/assets/js/appMixins.js'
import { nativeGo, nativeCommentRankFavorites} from '@/assets/js/bridge.js'; // 引入桥
import { isApp } from '@/assets/js/env'

import AddRankCard from '@/components/add-rank-card.vue'; //加入榜单
import ProductBar from '@/components/product-bar.vue'; //产品条

export default {
  name:'rank-details',
  mixins:[appMixins],  
  components:{
    AddRankCard,ProductBar
  },
  data(){
    return {
      data: {
        "id": 1,
        "title": "传奇摇滚音频品牌 Marshall 推出中国限量版蓝牙音箱",
        "content": "Marshall 诞生于上个世纪 60 年代的英国伦敦。 第一款 Marshall 扩音器由品牌创始人 Jim Marshall 在一间小作坊内研制而成， 而现在 Marshall 已经成为了世界顶级的音箱制造商。 摇滚史上不少伟大的吉他手如 Jimi Hendrix、 Eric Clapton、 Jimmy Page 都曾借助 Marshall 音箱为世人留下了不朽的音乐作品。 如今， Marshall 旗下的蓝牙音箱及耳机产品也同样凭借着优秀的音质和经典的设计受到市场欢迎",
        "cover_img_url": "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1526623210095&di=b6b1afd7c32941696221d0a968736748&imgtype=0&src=http%3A%2F%2Fp3.gexing.com%2FG1%2FM00%2F39%2F56%2FrBACFFOJZ0mDlDkZAAAcODzXYPQ517.jpg",
        "favs": 4,
        "sort": 2,
        "fav_state":1,
        "created_at": 1525949499,
        "updated_at": 1525949499
      },
      productDatas:[
        {
          "id": 3,
          "imgs_url": [
            "http://dev.backend.dgtle.com"
          ],
          "title": "iphone6s plus",
          "score": 0,
          "issue_date": "1970-01-01",
          "tags": [{ "id": 19, "title": "小米", "icon": "http://img.whalegogo.com/tag/20180124/14bed9fd09e41ef7c0eda81b9d8c40b2.jpg", "catalog": "数码" }, { "id": 20, "title": "大米", "icon": "http://img.whalegogo.com/tag/20180124/14bed9fd09e41ef7c0eda81b9d8c40b2.jpg", "catalog": "数码" } ],
          "productTypes": null
        },
        {
          "id": 4,
          "imgs_url": [
            "http://dev.backend.dgtle.com"
          ],
          "title": "iphone6s plus",
          "score": 0,
          "issue_date": "1970-01-01",
          "tags": [{ "id": 19, "title": "小米", "icon": "http://img.whalegogo.com/tag/20180124/14bed9fd09e41ef7c0eda81b9d8c40b2.jpg", "catalog": "数码" }, { "id": 20, "title": "大米", "icon": "http://img.whalegogo.com/tag/20180124/14bed9fd09e41ef7c0eda81b9d8c40b2.jpg", "catalog": "数码" } ],
          "productTypes": null
        },
      ]

    }
  },
  methods:{
    favorites(_isCancel,_id){//_isCancel == 取消收藏
      this.data.fav_state = !this.data.fav_state
      if(!_isCancel){
        this.data.favs++
      }else{
        this.data.favs--
      }
      nativeCommentRankFavorites({board_id:_id},_isCancel)
      if(!isApp){}
      
    }

  }
}
</script>

<style scoped lang="less">
@m:750/375;
.RankDetails{}
  .RankDetails-bgShadow{
    position: absolute;
    top: 0;left: 0;
    width:100%;height: 100%;
    background-image: linear-gradient(-180deg, rgba(0,0,0,0.30) 2%, rgba(0,0,0,0.00) 100%);
  }
  .RankDetails-bg{
    width:100%;
  }
  .RankDetails-content{
    padding:0 20px*@m;
  }
    .RankDetails-h1{
      margin-top:25px*@m;
      font-family: PingFangSC-Semibold;
      font-size: 28px*@m;
      color: #0F2540;
      letter-spacing: 0;
      line-height: 38px*@m;
    }
    .RankDetails-p{
      margin-top:40px*@m;
      font-family: PingFangSC-Regular;
      font-size: 16px*@m;
      color: #0F2540;
      letter-spacing: 0;
      text-align: justify;
      line-height: 26px*@m;
    }
    .RankDetails-collectionBtn{
      display: inline-block;
      margin-top:20px*@m;
      padding:0 23px*@m;
      color:#fff;font-size: 16px*@m;
      font-family: PingFangSC-Medium;
      line-height: 40px*@m;
      background-image: linear-gradient(-180deg, #FFE57B 0%, #FFC745 100%);
      border-radius: 100px*@m;
    }
    .RankDetails-collectionCount{
      margin-top:10px*@m;
      font-family: PingFangHK-Regular;
      font-size: 13px*@m;
      color: #BCBCBC;
      text-align: center;
      line-height: 13px*@m;
    }


</style>



