import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'
// import CommentList from '@/view/comment-list.vue'; //点评列表
import RankDetails from '@/view/comment/comment-rank-details.vue'; //榜单详情

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/rank-details.html',
  routes: [
    {
      path: '/',
      name: 'RankDetails',
      component: RankDetails
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


